.page-footer__nav {
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 50px;
  padding-bottom: 65px;
  column-gap: 20px;
  row-gap: 20px; }

.page-footer__copyright {
  margin: 0; }

.social {
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.social__slogan {
  margin: 0;
  margin-right: 40px;
  font-weight: 700;
  font-size: 24px;
  line-height: normal;
  font-family: "Georgia", "Arial", serif;
  color: #a38b70; }

.social__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 300px;
  margin: 0;
  padding: 0;
  list-style: none;
  column-gap: 10px;
  row-gap: 10px; }

.social__item:last-child {
  margin-right: 0; }

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 62px;
  height: 62px;
  border: 1px solid rgba(255, 255, 255, 0.1); }
  .social__link::before {
    content: "";
    display: block;
    background-repeat: no-repeat;
    background-position: center center; }
  .social__link--vk::before {
    width: 18px;
    height: 21px;
    background-image: url("../img/icon-vk.png"); }
  .social__link--fb::before {
    width: 13px;
    height: 27px;
    background-image: url("../img/icon-facebook.png"); }
  .social__link--inst::before {
    width: 24px;
    height: 24px;
    background-image: url("../img/icon-instagram.png"); }
  .social__link:hover, .social__link:focus {
    border-color: rgba(255, 255, 255, 0.3); }
  .social__link:active {
    border-color: rgba(255, 255, 255, 0.1); }
    .social__link:active::before {
      opacity: 0.3; }

.price-list {
  border-collapse: collapse;
  font-size: 18px;
  line-height: 30px; }
  .price-list tr {
    display: flex;
    justify-content: space-between; }
    .price-list tr > *:nth-child(2) {
      flex-grow: 1; }
  .price-list td {
    padding: 10px 15px;
    border: 1px solid #d8d8d8; }
  .price-list th {
    padding: 10px 15px; }

.price-list__head th {
  font-weight: 400;
  font-size: 14px;
  text-align: left;
  text-transform: uppercase; }
  .price-list__head th:first-child {
    text-align: center; }

.price-list__number {
  text-align: center; }

.price-list__service {
  font-weight: 700; }

.price-list__master {
  width: 340px; }

.price-list__value {
  width: 170px; }

.write-us-form {
  padding: 0; }

.write-us-form__message {
  display: block;
  margin: 0;
  padding: 0;
  border: none; }

.write-us-form__user-info {
  display: block;
  margin: 0;
  margin-bottom: 25px;
  padding: 0;
  border: none;
  border-bottom: 1px solid #d5d5d5; }

.write-us-form__group {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  column-gap: 50px; }

.write-us-form__name {
  width: 100%;
  margin: 0;
  margin-bottom: 15px; }

.write-us-form__phone {
  flex-grow: 1;
  margin: 0;
  margin-bottom: 30px; }

.write-us-form__email {
  flex-grow: 1;
  margin: 0;
  margin-bottom: 30px; }

.write-us-form__sub-toggler {
  margin: 0;
  margin-bottom: 28px;
  padding-right: 16px;
  padding-left: 16px; }

.write-us-form__message-theme {
  margin: 0;
  margin-bottom: 15px; }

.write-us-form__message-text {
  margin: 0;
  margin-bottom: 54px; }

.write-us-form__submit {
  min-width: 170px; }

.copyright {
  font-size: 18px;
  color: #ffffff;
  text-transform: uppercase; }

.copyright__heart-icon {
  display: inline-block;
  width: 27px;
  height: 24px;
  font-size: 0;
  line-height: 24px;
  vertical-align: top;
  background-image: url("../img/icon-heart.png");
  background-repeat: no-repeat;
  background-position: center center; }

.copyright__link {
  position: relative;
  color: #ffffff; }
  .copyright__link::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -5px;
    left: 0;
    display: none;
    height: 2px;
    background-color: #a38b70; }
  .copyright__link:hover::after, .copyright__link:focus::after {
    display: block; }
  .copyright__link:active {
    color: rgba(255, 255, 255, 0.3); }
    .copyright__link:active::after {
      background-color: #a38b70; }
