.page-footer {
  color: $light-gray-text-color;

  background-color: $dark-gray-bg;
}

.page-footer__wrapper {
  padding-top: 45px;
  padding-bottom: 50px;

  @include container;
}

.page-footer__section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 45px;
  padding-bottom: 45px;

  border-bottom: 2px solid #5e5e5e;

  &--top {
    align-items: flex-start;
  }

  &:last-child {
    margin-bottom: 0;
    padding-bottom: 0;

    border-bottom: none;
  }
}

.page-footer__logo {
  display: block;
  width: 205px;
  height: 55px;

  img {
    display: block;
  }
}

.page-footer__order-button {
  min-width: 170px;
}

.page-footer__nav {
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 50px;
  padding-bottom: 65px;

  column-gap: 20px;

  row-gap: 20px;
}

.page-footer__copyright {
  margin: 0;
}
